.navbar{
  border-bottom: 2px;
  border-bottom-color: #ff4c49;
  padding: 0.2em;
}
.nav-link, .item{
    font-family: "Futura", sans-serif;
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 3px;
    padding: 15px;
  }

  .nav-item{
    width: 50%;
  }
  .navbar-nav{
    margin: auto;
    width: 50%;
    text-align: center;
  }

  .navbar-light .navbar-nav .nav-link:hover, .navbar-light .navbar-nav .nav-link:focus, .nav-item a.chosen,
  .nav-item a:hover {
    color: #ff4c49;
  }

  .card{
    background: #fff5f2;
    font-family: "Futura", sans-serif;
    color: #ff4c49;
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 3px;
    padding: 70px;
    border: none;
  }

  h1{
    padding: 50px;
    font-weight: bold;
    text-align: center;
    letter-spacing: 4px;
  }

  /*Menu component*/
  button.item:focus, .item a.chosen,
  .item a:hover {
    border-bottom-width: 1px;
    border-bottom-color: #F14B31;
    border-bottom-style: solid;
    outline: none;
  }

  /*Graph components*/
  .graph {
    color: #575A89;
  }

  .ui.circular.button {
    border-radius: 20em;
  }

  i.icon, i.icons {
    font-size: 2em;
  }

  .ui.button{
    line-height: 2em;
  }

  .ui.card {
    width: 300px;
    height: 200px;
  }

  /*Teaser Cards*/
  .teaserlink, a:hover {
    color: #575A89;
    text-decoration: none;
    font-size: 10pt;
    padding-top: 5%;
  }

  .teaser-img {
    max-height: 150px;
    max-width: 150px;
    padding: 5%;
  }